body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #fff;
  min-height: 100vh;
}
input {
  width: 90%;
  border: 1px solid;
  height: 30px;
  font-size: 15px;
	outline: none;
	padding: 5px 10px 5px 10px;
	border-radius: 5px;
}

.date-time {
  font-size: 12px;
  color: rgb(211, 211, 211);
  font-weight: 400;
  margin-left: 15px;
}

.ChatBoard-container {
  background: #ffffff;
  border-radius: 3px;
  width: calc(78% - 30px);
  height: 80vh;
  display: -ms-flexbox;
  display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
  margin-top: 50px;
  margin-left: 200px;

}

.ChatBoard-message-parent-container {
  padding: 5px;
  padding-bottom: 10px;
  height: calc(90% - 20px);
  width: 100%;
  border-radius: 3px;
}

.ChatBoard-message-child-container {
  padding: 15px;
  padding-bottom: 0px;
  height: calc(99% - 2px);
  width: calc(100% - 30px);
  background-color: #fff;
  border-radius: 3px;
  overflow-x: hidden;
  overflow-y: auto;
}

.ChatBoard-message-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  height: 9%;
  margin-top: 5px;
  width: 100%;
  padding: 0px 200px 0px 0px;
}

.form-message-container {
  position: absolute;
  bottom: 0;
  margin-bottom: 40px;
  width: 1000px;
  margin-left: 100px;
}

.App-container {
  height: 780px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 30px;
}



